.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h2 {
	margin-bottom: 0px;
}

input {
  display: inline-block;
  margin-right: 10px;
  margin-left: 10px;
}

.title {
	background-color: #282c34;
	color: white;
	border: none;
	border-bottom: 1px solid grey;
}

.taskName {
  display: inline-block;
}

button {
  margin-left: 10px;
}

.content {
	padding: 0px 35px 0px 35px;
}

.red {
	color: red;
}

.bin {
	background:linear-gradient(to bottom, #c62d1f 5%, #f24437 100%);
	background-color:#c62d1f;
	border-radius:18px;
	border:1px solid #d02718;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:5px 10px;
	text-decoration:none;
	text-shadow:0px 1px 0px #810e05;
}

.localSave, .items {
	background:linear-gradient(to bottom, #2dc61f 5%, #4df237 100%);
	background-color:#2dc61f;
	border-radius:18px;
	border:1px solid #4df237;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:5px 10px;
	text-decoration:none;
	text-shadow:0px 1px 0px #26aa1a;
}

.localStorage {
	margin-top: 5px;
	margin-bottom: 30px;
}